"use client";

import { useState, useEffect, useRef } from "react";
import { motion } from "framer-motion";
import Image from "next/image";
import { BiChevronLeft, BiChevronRight } from "react-icons/bi";
import { FiCalendar, FiMapPin, FiYoutube } from "react-icons/fi";
import { eventService } from "@/services/eventService";

export default function EventsSection() {
  const [events, setEvents] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const scrollContainerRef = useRef(null);
  const [showLeftArrow, setShowLeftArrow] = useState(false);
  const [showRightArrow, setShowRightArrow] = useState(true);

  useEffect(() => {
    const fetchEvents = async () => {
      try {
        setIsLoading(true);
        const response = await eventService.getEvents({
          status: 'published',
          isActive: true,
          limit: 6
        });
        setEvents(response.data || []);
      } catch (error) {
        console.error("Failed to fetch events:", error);
        setEvents([]);
      } finally {
        setIsLoading(false);
      }
    };

    fetchEvents();
  }, []);

  // Helper function to get image URL
  const getImageUrl = (imagePath) => {
    if (!imagePath) return '/images/c1.jpg';
    if (imagePath.startsWith('http')) return imagePath;
    const apiUrl = process.env.NEXT_PUBLIC_API_URL?.replace('/api', '') || 'http://localhost:5000';
    return `${apiUrl}${imagePath.startsWith('/') ? imagePath : '/' + imagePath}`;
  };

  // Format date
  const formatDate = (dateString) => {
    if (!dateString) return 'TBD';
    try {
      const date = new Date(dateString);
      return date.toLocaleDateString('en-IN', {
        day: 'numeric',
        month: 'short',
        year: 'numeric'
      });
    } catch {
      return dateString;
    }
  };

  // Scroll functionality
  const checkScrollButtons = () => {
    if (scrollContainerRef.current) {
      const { scrollLeft, scrollWidth, clientWidth } = scrollContainerRef.current;
      setShowLeftArrow(scrollLeft > 0);
      setShowRightArrow(scrollLeft < scrollWidth - clientWidth - 10);
    }
  };

  useEffect(() => {
    checkScrollButtons();
    const container = scrollContainerRef.current;
    if (container) {
      container.addEventListener('scroll', checkScrollButtons);
      window.addEventListener('resize', checkScrollButtons);
      return () => {
        container.removeEventListener('scroll', checkScrollButtons);
        window.removeEventListener('resize', checkScrollButtons);
      };
    }
  }, [events]);

  const scrollLeft = () => {
    if (scrollContainerRef.current) {
      scrollContainerRef.current.scrollBy({
        left: -400,
        behavior: 'smooth'
      });
    }
  };

  const scrollRight = () => {
    if (scrollContainerRef.current) {
      scrollContainerRef.current.scrollBy({
        left: 400,
        behavior: 'smooth'
      });
    }
  };

  if (isLoading) {
    return (
      <section className="py-16 bg-gradient-to-br from-purple-50 via-white to-blue-50">
        <div className="max-w-8xl mx-auto px-4">
          <div className="text-center mb-12">
            <h2 className="text-3xl md:text-4xl font-extrabold text-gray-900">Upcoming Events</h2>
            <p className="text-gray-600 mt-2">Loading events...</p>
          </div>
        </div>
      </section>
    );
  }

  if (events.length === 0) {
    return null; // Don't show section if no events
  }

  return (
    <section className="py-20 bg-gradient-to-br from-purple-50 via-white to-blue-50 border-t-2 border-b-2 border-purple-200">
        <div className="max-w-8xl mx-auto px-4">
        {/* Section Header */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          whileInView={{ opacity: 1, y: 0 }}
          viewport={{ once: true }}
          transition={{ duration: 0.6 }}
          className="text-center mb-14"
        >
          <h2 className="text-4xl font-extrabold text-gray-900 mb-3">
            Upcoming Events
          </h2>
          <p className="text-lg text-gray-600 max-w-2xl mx-auto">
            Join us in making a difference. Explore our upcoming events and be part of the change.
          </p>
          <div className="mt-4 w-24 h-1 bg-gradient-to-r from-purple-500 to-blue-500 mx-auto rounded-full" />
        </motion.div>

        {/* Events Scrollable Container */}
        <div className="relative">
          {/* Left Scroll Button */}
          {showLeftArrow && (
            <button
              onClick={scrollLeft}
              className="absolute left-4 top-1/2 -translate-y-1/2 z-20 bg-white/95 hover:bg-white shadow-xl rounded-full p-2 md:p-3 transition-all duration-300 hover:scale-110 border-2 border-gray-300 hover:border-purple-400"
              aria-label="Scroll left"
            >
              <BiChevronLeft className="text-2xl md:text-3xl text-gray-700" />
            </button>
          )}

          {/* Scrollable Container */}
          <div
            ref={scrollContainerRef}
            className="flex gap-6 overflow-x-auto scrollbar-hide pb-4 scroll-smooth"
            style={{ paddingLeft: '1rem', paddingRight: '1rem' }}
          >
          {events.map((event, index) => (
            <motion.div
              key={event._id}
              initial={{ opacity: 0, y: 40 }}
              whileInView={{ opacity: 1, y: 0 }}
              viewport={{ once: true }}
              transition={{ delay: index * 0.1, duration: 0.5 }}
              className="flex-shrink-0 w-80 bg-white rounded-2xl shadow-lg overflow-hidden hover:shadow-2xl transition-all duration-300 transform hover:-translate-y-2 border-2 border-gray-200 hover:border-purple-400 relative group"
            >
              {/* Gradient border effect on hover */}
              <div className="absolute inset-0 rounded-2xl bg-gradient-to-r from-purple-400 via-blue-400 to-pink-400 opacity-0 group-hover:opacity-20 transition-opacity duration-300 -z-10 blur-sm"></div>
              {/* Event Image */}
              <div className="relative w-full h-48 overflow-hidden">
                {event.picture?.url ? (
                  <img
                    src={getImageUrl(event.picture.url)}
                    alt={event.heading}
                    className="w-full h-full object-cover hover:scale-110 transition-transform duration-500"
                  />
                ) : (
                  <div className="w-full h-full bg-gradient-to-br from-purple-400 to-blue-500 flex items-center justify-center">
                    <span className="text-white text-4xl">üìÖ</span>
                  </div>
                )}
                <div className="absolute inset-0 bg-gradient-to-t from-black/60 via-transparent to-transparent" />
                
                {/* Date Badge */}
                <div className="absolute top-4 left-4 bg-white/90 backdrop-blur-sm px-4 py-2 rounded-lg">
                  <div className="flex items-center gap-2 text-gray-800">
                    <FiCalendar className="text-purple-600" size={18} />
                    <span className="text-sm font-semibold">{formatDate(event.date)}</span>
                  </div>
                </div>
              </div>

              {/* Event Content */}
              <div className="p-6">
                {/* Heading */}
                <h3 className="text-xl font-bold text-gray-900 mb-3 line-clamp-2">
                  {event.heading}
                </h3>

                {/* Short Brief */}
                <p className="text-gray-600 text-sm mb-4 line-clamp-2">
                  {event.shortBrief}
                </p>

                {/* Location */}
                {event.location && (
                  <div className="flex items-center gap-2 text-gray-600 mb-4">
                    <FiMapPin size={16} className="text-purple-600" />
                    <span className="text-sm">{event.location}</span>
                  </div>
                )}

                {/* Videos Count */}
                {event.videos && event.videos.length > 0 && (
                  <div className="flex items-center gap-2 text-gray-600 mb-4">
                    <FiYoutube size={16} className="text-red-600" />
                    <span className="text-sm">{event.videos.length} Video(s)</span>
                  </div>
                )}

                {/* Read More Button */}
                <button className="w-full mt-4 bg-gradient-to-r from-purple-600 to-blue-600 text-white py-2 px-4 rounded-lg font-semibold hover:from-purple-700 hover:to-blue-700 transition-all transform hover:scale-105">
                  View Details
                </button>
              </div>
            </motion.div>
          ))}
          </div>

          {/* Right Scroll Button */}
          {showRightArrow && (
            <button
              onClick={scrollRight}
              className="absolute right-4 top-1/2 -translate-y-1/2 z-20 bg-white/95 hover:bg-white shadow-xl rounded-full p-2 md:p-3 transition-all duration-300 hover:scale-110 border-2 border-gray-300 hover:border-purple-400"
              aria-label="Scroll right"
            >
              <BiChevronRight className="text-2xl md:text-3xl text-gray-700" />
            </button>
          )}
        </div>

        {/* View All Events Button */}
        {events.length >= 6 && (
          <div className="text-center mt-12">
            <button className="px-8 py-3 bg-gradient-to-r from-purple-600 to-blue-600 text-white font-semibold rounded-lg hover:from-purple-700 hover:to-blue-700 transition-all transform hover:scale-105 shadow-lg">
              View All Events
            </button>
          </div>
        )}
      </div>
    </section>
  );
}

