import api from '../utils/api';

// Coupon Service
export const couponService = {
  // Get all coupons
  getCoupons: async (params = {}) => {
    try {
      const response = await api.get('/coupons', { params });
      return response.data;
    } catch (error) {
      throw error.response?.data || error;
    }
  },

  // Get single coupon
  getCoupon: async (id) => {
    try {
      const response = await api.get(`/coupons/${id}`);
      return response.data;
    } catch (error) {
      throw error.response?.data || error;
    }
  },

  // Create coupon
  createCoupon: async (couponData) => {
    try {
      const response = await api.post('/coupons', couponData);
      return response.data;
    } catch (error) {
      throw error.response?.data || error;
    }
  },

  // Update coupon
  updateCoupon: async (id, couponData) => {
    try {
      const response = await api.put(`/coupons/${id}`, couponData);
      return response.data;
    } catch (error) {
      throw error.response?.data || error;
    }
  },

  // Delete coupon
  deleteCoupon: async (id) => {
    try {
      const response = await api.delete(`/coupons/${id}`);
      return response.data;
    } catch (error) {
      throw error.response?.data || error;
    }
  },

  // Redeem coupon
  redeemCoupon: async (id, redemptionData) => {
    try {
      const response = await api.post(`/coupons/${id}/redeem`, redemptionData);
      return response.data;
    } catch (error) {
      throw error.response?.data || error;
    }
  },

  // Get coupon by code
  getCouponByCode: async (code) => {
    try {
      const response = await api.get(`/coupons/code/${code}`);
      return response.data;
    } catch (error) {
      throw error.response?.data || error;
    }
  },

  // Get user's coupons
  getUserCoupons: async (params = {}) => {
    try {
      const response = await api.get('/coupons/my-coupons', { params });
      return response.data;
    } catch (error) {
      throw error.response?.data || error;
    }
  },

  // Get coupon analytics
  getCouponAnalytics: async (id) => {
    try {
      const response = await api.get(`/coupons/${id}/analytics`);
      return response.data;
    } catch (error) {
      throw error.response?.data || error;
    }
  },

  // Get coupons by category
  getCouponsByCategory: async (category) => {
    try {
      const response = await api.get('/coupons', { 
        params: { category, limit: 12 } 
      });
      return response.data;
    } catch (error) {
      throw error.response?.data || error;
    }
  }
};
