"use client";

import React from "react";
import { motion } from "framer-motion";
import { FaCubes, FaSearch, FaBalanceScale } from "react-icons/fa";
import { BiRupee } from "react-icons/bi";

const leftFeatures = [
  {
    title: "Passion-Driven Mission",
    desc: "Our strategy is based on responsibility, openness, and teamwork. We think that understanding and empathy are the first steps toward true change.",
    icon: FaCubes,
  },
  {
    title: "Transparency",
    desc: "Our NGO prioritizes transparency through regular public reporting, financial accountability, and open communication.",
    icon: FaSearch,
  },
];

const rightFeatures = [
  {
    title: "Accountability",
    desc: "Our NGO upholds accountability through transparent financial practices, regular reporting, and a commitment to ethical conduct, ensuring responsible and trustworthy operations.",
    icon: FaBalanceScale,
  },
  {
    title: "Fundraising for Impact",
    desc: "Our NGO focuses on impactful fundraising efforts, channeling resources to make a meaningful difference in our mission and create positive outcomes.",
    icon: BiRupee,
  },
];

export default function WhyChooseUsSection() {
  return (
    <section className="py-16 bg-gradient-to-br from-gray-50 via-white to-blue-50 border-t-2 border-b-2 border-gray-300 dark:bg-gray-900 transition-colors duration-500">
      <div className="max-w-8xl mx-auto px-4">
        
        <div className="text-center max-w-2xl mx-auto mb-12">
          <h5 className="text-blue-600 dark:text-blue-400 font-bold uppercase text-lg md:text-xl mb-2">
            Why Choose Us
          </h5>
          <h1 className="text-2xl md:text-4xl font-bold text-gray-900 dark:text-white">
            Our Work Promise To Uphold The Trust Placed
          </h1>
        </div>

        <div className="grid grid-cols-1 lg:grid-cols-3 gap-8 items-center">
          
          <div className="space-y-8">
            {leftFeatures.map((feature, idx) => {
              const Icon = feature.icon;
              return (
                <motion.div
                  key={idx}
                  initial={{ opacity: 0, y: 50 }}
                  whileInView={{ opacity: 1, y: 0 }}
                  transition={{ delay: idx * 0.2, duration: 0.6 }}
                  viewport={{ once: true }}
                  className="flex flex-col items-center text-center lg:items-start lg:text-left bg-white/80 backdrop-blur-sm rounded-xl p-6 border-2 border-gray-200 hover:border-blue-400 transition-all duration-300 hover:shadow-xl group"
                >
                  <div className="bg-gradient-to-r from-blue-600 to-indigo-600 dark:bg-blue-500 rounded-full flex items-center justify-center w-16 h-16 mb-4 text-white text-xl shadow-lg group-hover:scale-110 transition-transform duration-300">
                    <Icon />
                  </div>
                  <h4 className="text-lg md:text-xl font-semibold text-gray-900 dark:text-white mb-2 group-hover:text-blue-600 transition-colors duration-300">
                    {feature.title}
                  </h4>
                  <p className="text-gray-700 dark:text-gray-300 text-sm md:text-base leading-relaxed">
                    {feature.desc}
                  </p>
                </motion.div>
              );
            })}
          </div>

          <motion.div
            initial={{ opacity: 0, y: 50 }}
            whileInView={{ opacity: 1, y: 0 }}
            transition={{ delay: 0.2, duration: 0.6 }}
            viewport={{ once: true }}
            className="relative h-96 w-full flex justify-center items-center"
          >
            <img
              src="/images/whychooseus.webp" 
              alt="Why Choose Us"
              className="h-full w-full object-contain rounded-lg shadow-xl border-2 border-gray-200 hover:border-blue-400 transition-all duration-300 hover:shadow-2xl"
            />
          </motion.div>

          <div className="space-y-8">
            {rightFeatures.map((feature, idx) => {
              const Icon = feature.icon;
              return (
                <motion.div
                  key={idx}
                  initial={{ opacity: 0, y: 50 }}
                  whileInView={{ opacity: 1, y: 0 }}
                  transition={{ delay: idx * 0.2, duration: 0.6 }}
                  viewport={{ once: true }}
                  className="flex flex-col items-center text-center lg:items-start lg:text-left bg-white/80 backdrop-blur-sm rounded-xl p-6 border-2 border-gray-200 hover:border-blue-400 transition-all duration-300 hover:shadow-xl group"
                >
                  <div className="bg-gradient-to-r from-blue-600 to-indigo-600 dark:bg-blue-500 rounded-full flex items-center justify-center w-16 h-16 mb-4 text-white text-xl shadow-lg group-hover:scale-110 transition-transform duration-300">
                    <Icon />
                  </div>
                  <h4 className="text-lg md:text-xl font-semibold text-gray-900 dark:text-white mb-2 group-hover:text-blue-600 transition-colors duration-300">
                    {feature.title}
                  </h4>
                  <p className="text-gray-700 dark:text-gray-300 text-sm md:text-base leading-relaxed">
                    {feature.desc}
                  </p>
                </motion.div>
              );
            })}
          </div>
        </div>
      </div>
    </section>
  );
}


